import React, { Component } from 'react';
import * as action from 'action';
import { connect } from 'react-redux';
import './About.css';
import $ from 'jquery';
import AboutDetail from './AboutDetail';
import { withTranslation, /*useTranslation*/ } from 'react-i18next';
import i18n from 'utils/i18n';

class About extends Component {
  constructor(props){
    super(props);

    if (this.props.history != null) {
      this.props.setHistory(this.props.history);
   }

    this.state = {
      index: 0,
    }

    this.district = [
      { id: 'pyeongchangeup', 
        position: {left: '120px', top: '384px'} },
      { id: 'mitanmyeon',
        position: {left: '193px', top: '410px'} },
      { id: 'bangnimmyeon', 
        position: {left: '68px', top: '310px'} },
      { id: 'daehwamyeon', 
        position: {left: '150px', top: '280px'} },
      { id: 'bongpyeonmyeon', 
        position: {left: '82px', top: '190px'} },
      { id: 'yongpyeonmyeon', 
        position: {left: '166px', top: '160px'} },
      { id: 'jinbumyeon', 
        position: {left: '233px', top: '190px'} },
      { id: 'degwallyeong', 
        position: {left: '310px', top: '126px'} },
    ];
  }

  componentDidUpdate(){
    console.log('componentDidUpdate');
    $('#dv-about-body').scrollTop(0);
  }

  onClick (index) {
    //if (this.idx !== )
    var linkData = {
        pathname: `../about/${this.district[index].id}`,
        state: {
          ...this.props.location.state,
          //path: path,
        }
    }
    this.setState({
      ...this.state,
      index: index,
    });
    //this.props.
    this.props.history.replace(linkData);
  }

  renderTab(url, index, path, name){
    //var page = this.state.page ? this.state.page : this.district[0].id;
    if (!url) {
      url = this.district[0].id;
    }
    var isFocus = this.state.index === index;
    if (this.district[index]) {

      path = path ? path : this.district[index].id;
      name = name ? name : this.props.t(`about.${path}`);
      
      if (isFocus && this.district[index].id !== url) {
        isFocus = false;
      }
      if (!isFocus && this.district[index].id === url) {
        isFocus = true;
      }
    }
    //var isBottom = index >= 4;
    var className = index === 4 ? 'bottom-left' : ( index === 7 ? 'bottom-right' : '');
    className += i18n.language === 'en' ? ' en' : '';
    return (
      <span className={(isFocus ? 'on ' : '') + className} 
        onClick={ () => { this.onClick(index); }}>
        <div>
          {name}
        </div>
      </span>);
  }

  renderIndicator (){
    var style = {left: this.state.indicatorLeft};
    return (<div className='indicator transition-default'> 
      <div id='dv-indicator-bar' style={style} className={'transition-fast'}/>
      <div id='dv-indicator-bar' style={style} className={'transition-x2'}/>
      <div id='dv-indicator-bar' style={style} className={'transition-default'}/>
    </div>);
  }

  renderItems(){
    var result = [];

    const { t } = this.props;

    this.district.forEach((element, index) => {
      result.push(<div className='district-item'           
        onClick={()=>{this.onClick(index)}}
        style={element.position}>{t(`about.${element.id}`)}</div>);
    });
    return result;
  }
  renderMain() {
    var scale = window.innerWidth > 400 ? window.innerWidth / 400 : 1;
    var offset = (498 * scale * 0.5) - (498 * 0.5);
    var style = {
      transform: `scale(${scale})`,
      backgroundImage: 'url(/resources/map_pc.jpg)',
      top: offset,
    };

    return (
      <div id='dv-about-main' className='transition-default'>
        <div style={style} id='dv-about-map'>
          { this.renderItems() }
        </div>
      </div>
      );
  }

  renderDetail(url) {
    return (
      <div id='dv-about-body'>
        <div id='dv-about-tab'>
          {this.renderTab(url, 0)}
          {this.renderTab(url, 1)}
          {this.renderTab(url, 2)}
          {this.renderTab(url, 3)}
          {this.renderTab(url, 4)}
          {this.renderTab(url, 5)}
          {this.renderTab(url, 6)}
          {this.renderTab(url, 7)}
        </div>
        <div id='dv-about-body-detail'>
          <AboutDetail id={ url ? url : this.district[this.state.index].id }/>
        </div>
      </div>);
  }

  render(){
    var { url } = this.props.match;
    url = url.split('/')[2];
    
    return (
      <div id='dv-about'>
        { url ? this.renderDetail(url) : this.renderMain() }
      </div>);
  }
}

const mapStateToProps = (state) => { return {}; }

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(withTranslation()(About));