import React, { Component } from 'react';
import './About.css';
import { withTranslation } from 'react-i18next';

class AboutDetail extends Component {
  constructor(props){
    super(props);

    this.state = {
    }
  }

  renderHistory(text){
    var splitText = text.split('\n');
    var result = [];

    splitText.forEach(t => {
      result.push(<li>{t}</li>);
    });
    return (result);
  }

  renderGeography(text, title){
    if (text) {
      return(
        <div>
          <h5>{title}</h5>
          <div id='dv-about-detail-text'>
            {text}
          </div>
        </div>
        );
    }
    return;
  }

  render() {
    const { t } = this.props;
    var id = this.props.id;
    var map00 = t(`about.data.${id}.map00`);
    var map01 = t(`about.data.${id}.map01`);
    var title = t(`about.${id}`);
    var history = t(`about.data.${id}.history`);
    var text = t(`about.data.${id}.text`);
    var geography = t(`about.data.${id}.geography`);

    return (
      <div id='dv-about-detail'>
        <h5 className="titLv">{title}</h5>
    
        <div id='dv-about-detail-img'>
          <img className='float-left' alt="평창읍 지도" src={map00}/>
          <img className='float-right' alt="평창군청 주변 약도 이미지" src={map01}/>
        </div>

        <h5>{t('about.history')}</h5>
        <ul>
          { this.renderHistory(history) }
        </ul>
        {this.renderGeography(geography, t('about.geography'))}
        <h5>{t('about.infomation')}</h5>
        <div id='dv-about-detail-text'>
          {text}
        </div>
      </div>);
  }
}

export default withTranslation()(AboutDetail);