import React, { Component } from 'react';
import './LocaleDropdown.css';
import i18n from 'utils/i18n';
//import { kr } from 'locales';

class LocaleDropdown extends Component {
  constructor(props){
    super(props);

    this.state = {
      language: i18n.language,
    }
  }

  changeLanguage (e) {
    if (this.state.language !== e)
    {
      i18n.changeLanguage(e);
      this.setState({
        ...this.state,
        language: e,
      })
    }
  }

  componentDidMount(){  
    window.initDropdown('.dropdown-trigger');
  }

  render(){
    return (
      <div id='dv-locale-dropdown'>
        <div class='dropdown-trigger' href='#' data-target='dropdown1'>
          <img src={`/resources/locale/${this.state.language}.png`}/>
        </div>
        <ul id='dropdown1' class='dropdown-content'>
          <li><a onClick={ ()=> { this.changeLanguage('kr') } }>한국어</a></li>
          <li><a onClick={ ()=> { this.changeLanguage('en') } }>English</a></li>
          <li><a onClick={ ()=> { this.changeLanguage('ch') } }>简体中文</a></li>
          <li><a onClick={ ()=> { this.changeLanguage('jp') } }>日本語</a></li>
        </ul>
      </div>);
  }
  /*
  
        <div id='dv-button-locale' onClick={ ()=> { this.changeLanguage('en') } }></div>
        <div id='dv-button-locale' style={{left: '100px'}} onClick={ ()=> { this.changeLanguage('kr') } }>한국어</div>
        <div id='dv-button-locale' style={{left: '200px'}} onClick={ ()=> { this.changeLanguage('jp') } }>일본어</div>
        <div id='dv-button-locale' style={{left: '300px'}} onClick={ ()=> { this.changeLanguage('ch') } }>중국어</div>
  */
}

export default LocaleDropdown;