import React, { Component } from 'react';
import LinkButton from 'component/UI/LinkButton';
import { connect } from 'react-redux';
import * as action from 'action';
import { blankDiv } from 'utils';
import './Main.css';
//import $ from 'jquery';
import { withTranslation, /*useTranslation*/ } from 'react-i18next';
//import i18n from 'utils/i18n';

class Main extends Component {
  constructor(props){
    super(props);

    if (this.props.history != null) {
      this.props.setHistory(this.props.history);
    }
  }

  render(){
    const { t } = this.props;

    return (
      <div id='dv-main'>
        {blankDiv('50px')}
        <div id='dv-main-button-list' className={this.props.showMenu ? 'show': 'hide'}>
          <div id='dv-main-buttons'>
            <LinkButton idx={1} 
              icon={'map'}
              text={t("main.button_00")} 
              link='/about'/>
              
            <LinkButton idx={2} 
              text={t('main.button_01')} 
              icon={'tour'}
              link='/tour'/>
          </div>
          <div id='dv-main-buttons'>      
            <LinkButton idx={3} 
              text={t('main.button_02')} 
              icon={'location_on'}
              link='/poi'/>
              
            <LinkButton idx={4} 
              text={t('main.button_03')} 
              icon={'games'}
              link='/game'/>
          </div>
          <div style={{width: '80vw', marginLeft: '10vw', fontSize: '12px'}}>
            { blankDiv('570px') }
            <b>browser info: </b>
            <br/>
            { window.navigator.userAgent }
            <br/>
            <br/>          
            <b>os:</b>{` ${window.os}`}
            { blankDiv('70px') }
          </div>
        </div>
      </div>);
  }
}

const mapStateToProps = (state) => {
  return {
      //user: state.auth.user
      showMenu: state.showMenu
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) },
    setShowMenu: (showMenu) => { dispatch(action.setShowMenu(showMenu)) }
      //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(withTranslation()(Main));